import * as React from "react";

class Fireworks extends React.Component {
  width = 0;
  height = 0;

  style = {
    width: "100vw",
    height: "100vh",
    margin: "-8px",
    position: "absolute",
    background: "#000000",
    zIndex: -1,
  };
  letterStyle = {
    width: "100vw",
    height: "100vh",
    color: "#ffffff",
    position: "absolute",
  };
  toNewZealand = new Date("2023-01-07");

  componentDidMount() {
    this.setCanvasSize();
    window.addEventListener("resize", this.setCanvasSize, false);
  }

  setCanvasSize() {
    this.width = window.innerWidth;
    this.height = window.innerHeight;
    const canvas = document.getElementById("fireworks");
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
  }

  handleClick = (e) => {
    const canvas = document.getElementById("fireworks");
    const context = canvas.getContext("2d");
    const { clientX: x, clientY: y } = e;
    this.fire(context, x, y);
  };

  calcDays = () => {
    const gap = new Date().getTime() - this.toNewZealand.getTime();
    return Math.floor(gap / 1000 / 3600 / 24);
  };

  clearCanvas = (context) => {
    context.fillStyle = "#000000";
    context.fillRect(0, 0, this.width, this.height);
  };

  createFireworks = (x, y) => {
    const particles = [];
    var hue = Math.floor(Math.random() * 361);
    var hueVariance = 30;
    var count = 100;
    for (var i = 0; i < count; i++) {
      var p = {};
      var angle = Math.floor(Math.random() * 360);
      p.radians = (angle * Math.PI) / 180;

      p.x = x;
      p.y = y;

      p.speed = Math.random() * 10;
      p.radius = p.speed;

      p.size = Math.floor(Math.random() * 3) + 1;

      p.hue =
        Math.floor(Math.random() * (hue + hueVariance - (hue - hueVariance))) +
        (hue - hueVariance);
      p.brightness = Math.floor(Math.random() * 31) + 50;
      p.alpha = (Math.floor(Math.random() * 61) + 40) / 100;

      particles.push(p);
    }
    return particles;
  };

  drawFireWorks = (context, particles) => {
    this.clearCanvas(context);
    for (var i = 0; i < particles.length; i++) {
      var p = particles[i];
      p.x += Math.cos(p.radians) * p.radius;
      p.y += Math.sin(p.radians) * p.radius + 0.4;

      p.radius *= 1 - p.speed / 512;

      p.alpha -= 0.005;

      context.beginPath();
      context.arc(p.x, p.y, p.size, 0, Math.PI * 2, false);
      context.closePath();

      context.fillStyle =
        "hsla(" + p.hue + ", 100%, " + p.brightness + "%, " + p.alpha + ")";
      context.fill();
    }
  };

  fire = (context, x, y) => {
    var rid;
    const particles = this.createFireworks(x, y);
    const tick = () => {
      context.globalCompositeOperation = "destination-out";
      context.fillStyle = "rgba(0,0,0," + 10 / 100 + ")";
      context.fillRect(0, 0, this.width, this.height);
      context.globalCompositeOperation = "lighter";

      this.drawFireWorks(context, particles);
      rid = requestAnimationFrame(tick);
    };
    cancelAnimationFrame(rid);
    tick();
  };

  render() {
    return (
      <div
        className="fireworks"
        style={this.style}
        onClick={this.handleClick}
        onKeyDown={() => {}}
      >
        <div style={this.letterStyle} onClick={this.handleClick}>
          <h1>Dear my love Huan</h1>
          <h2>We have been seperated for {this.calcDays()} days.</h2>
          <h2>I miss you!</h2>
          <p>
            I miss you all the time, so I want to do something memorable for
            us.I think about what can I do for us, and this blog is the result.
          </p>
          <p>
            Before you going to New Zealand, you said "You can write letter to
            me". I always remeber it, but you are a traveler in New Zealand,
            without a permanent address.
          </p>
          <p>
            This is my first letter to you, I will keep writing to you this way.
          </p>
          <p>
            By the way, I write this website by{" "}
            <a href={"https://www.gatsbyjs.com/"}>GatsbyJS</a> and
            <a href={"https://reactjs.org/"}>ReactJS</a>. They are easy to
            learn. And it is worth mentioning that GatsbyJS is a web framework
            for static page website. It supports transfering markdown file to
            web page.
          </p>
          <p>
            I want to share my life, my thinking, my English level and all of
            mine with you. This letter is completed with the help of Google...
          </p>
          <p>
            At last, I love you and I leave an egg for you, maybe you have found
            it XD. Do you like it?
          </p>
          <h3>DO NOT JUDGE UI! I WILL DO IT BETTER IN FUTURE!!!!</h3>
        </div>
        <canvas width={this.width} height={this.height} id="fireworks"></canvas>
      </div>
    );
  }
}

export default Fireworks;
